// app/api/whatsapp-request/route.ts

import { NextResponse } from 'next/server';

export async function POST(req: Request) {
  try {
    const body = await req.json();

    const { name, shop, contact, business, location } = body;

    if (!name || !shop || !contact || !business || !location) {
      return NextResponse.json({ error: 'All fields are required' }, { status: 400 });
    }

    // üëâ Here you could store to a database, send to email, Google Sheets, etc.
    console.log('üì© New WhatsApp form submission:', {
      name,
      shop,
      contact,
      business,
      location,
    });

    return NextResponse.json({ success: true });
  } catch (error) {
    console.error('‚ùå Error in POST /api/whatsapp-request:', error);
    return NextResponse.json({ error: 'Internal Server Error' }, { status: 500 });
  }
}
